<template>
  <div class="pagination-wrap mt-4">
    <ul>
      <li><a href="javascript:;" class="aw prev" :class="{'disabled':!pages.has_pre}" @click.prevent="updatePage(pages.current_page - 1)">Prev</a></li>
      <li v-for="page in pages.total_pages" :key="page"><a href="javascript:;" class="page-link" :class="{ 'active' : page === pages.current_page }" @click.prevent="updatePage(page)">{{ page }}</a></li>
      <li><a href="javascript:;" class="aw next" :class="{'disabled':!pages.has_next}" @click.prevent="updatePage(pages.current_page + 1)">Next</a></li>
    </ul>
  </div>
</template>

<script>
  export default {
    props: ['pages'],
    data(){
      return{

      }
    },
    methods:{
      updatePage(page){
        this.$emit('emit-page',page);
      },
    },
  }
</script>

